import React, { Component } from 'react';
import Calendar from 'react-calendar';


import './calendar.scss';


class CalendarComponent extends Component {

    constructor() {
        super();
        this.state = {
            date: new Date()
        };
    }

    onChange = (date) => {
        this.setState({ date });
        this.props.onCalendarChange(date);
    };


    render() {

        return (

            <div>
                {/* вот ссылка на api, твоя задача стилизовать этот календарь
                https://github.com/wojtekmaj/react-calendar#readme
              */}

                <Calendar className="right-calendar"
                          onChange={this.onChange}
                          value={this.state.date}
                />

            {/*    https://www.npmjs.com/package/react-time-picker
                нужно как-то еще время привязать
            */}

            </div>
        );
    }

}

export default CalendarComponent;
